/* eslint no-new-func: 0 */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import { openAuth, logoutUser } from '../actions/auth';
import C from '../constants';

class Auth extends Component {
	componentDidMount() {
		const jsCode = `
		(function defer() {
				 if (window.jQuery) {
							 $('.control-submit-trigger').click( function(){
								$('body').addClass('submit-active');
								$('.control-submit-trigger').addClass('submit-active');
							});

							$('.icon-close').click( function(){
								$('body').removeClass('submit-active');
								$('.control-submit-trigger').removeClass('submit-active');
							});

							$(window).scroll(function() {
							 if ($(this).scrollTop()>0) {
								 $('.auth--loggedout').fadeOut();
								 $('.auth--logging').fadeOut();
								 $('.auth--loggedin').fadeOut();
							 } else {
								 $('.auth--loggedout').fadeIn();
								 $('.auth--logging').fadeIn();
								 $('.auth--loggedin').fadeIn();
							 }
							});

				 } else {
							setTimeout(function() { defer() }, 50);
				 }
		})();
		`;
		new Function(jsCode)();
	}
	getJSX(props) {
		switch (props.auth.status) {
			case C.AUTH_LOGGED_IN: return (
				<div className="auth--wrapper auth--loggedin">
					{/* <span>Logged in as {props.auth.username}.</span> */}
					<button className="btn-submit control-submit-trigger">Submit plugin</button>
					{" "}<button className="btn-login" onClick={props.logoutUser}>Log out</button>
				</div>
			);
			case C.AUTH_AWAITING_RESPONSE: return (
				<div className="auth--wrapper auth--logging">
					<button className="btn-submit control-submit-trigger">Submit plugin</button>
					<button className="btn-login btn-loading" disabled>authenticating...</button>
				</div>
			);
			default: return (
				<div className="auth--wrapper auth--loggedout">
					<button className="btn-submit control-submit-trigger">Submit plugin</button>
					<button className="btn-login" onClick={props.openAuth}>Log in</button>
				</div>
			);
		}
	}
	render() {
		return this.getJSX(this.props);
	}
}

const mapStateToProps = (state) => {
	return { auth: state.auth };
};

const mapDispatchToProps = {
	openAuth,
	logoutUser,
};

export default connect(mapStateToProps, mapDispatchToProps)(Auth);



